<template>
  <div class="list-row d-flex justify-content-center mb-3">
    <h1 class="my-4">Pedidos</h1>
  </div>

  <div>
    <input type="date" v-model="startDate" />
    <input type="date" v-model="endDate" />
    <a
      class="
        list-group-item list-group-item-action list-group-item-light
        d-flex
        justify-content-between
        align-items-center
      "
      v-for="(item, index) in filteredData"
      :key="item"
      @click="push(item.id)"
    >
      <span class="d-flex align-items-center">{{ item.cliente }}</span>
      <span>{{ item.fecha }}</span>
      <span class="my-4">Pedido N # {{ index }}</span>
    </a>
  </div>
</template>

<script>
import { useLoadPedidos } from "@/firebase";
import { ref } from "vue"

export default {
  setup() {
    const pedidos = useLoadPedidos();
    var startDate = new Date();
    startDate = ref(new Date(startDate.getTime() - startDate.getTimezoneOffset() * 60000)
      .toISOString()
      .split("T")[0])
    var endDate = new Date();
    endDate = ref(new Date(endDate.getTime() - endDate.getTimezoneOffset() * 60000)
      .toISOString()
      .split("T")[0])
    

    return { pedidos, endDate, startDate };
  },
  methods: {
    push(id) {
      this.$router.push("/pedido/" + id);
    },
  },
  computed: {
    filteredData() {
      return this.pedidos.filter((item) => item.fecha <= this.endDate && item.fecha >= this.startDate);
    },
  },
};
</script>